*/
const gcd = (a: number) => (b: number): number => {
    // Inner function using tail-recursive optimization
    const gcdTailRec = (x: number, y: number): number =>
        y === 0 ? x : gcdTailRec(y, x % y);
    return gcdTailRec(a, b);
};

